import React, { useState, useRef } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Checkbox from "@material-ui/core/Checkbox";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";

import data from "../static/data";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    flexDirection: "column",
    marginBottom: theme.spacing(2),
    padding: "30px",
    borderRadius: "10px",
    width: "90%",
    backgroundColor: "#ffffff",
    margin: "20px auto",
    boxShadow: "rgba(0, 0, 0, 0.35) 0px 5px 15px",
    [theme.breakpoints.down("md")]: {
      flexDirection: "column",
      marginBottom: theme.spacing(2),
    },
  },
  select: {
    marginRight: theme.spacing(2),
    borderRadius: "10px",
    border: "1px solid #ccc",
  },
  searchButton: {
    textTransform: "none",
    marginBottom: theme.spacing(2),
  },
  table: {
    minWidth: "700px",
  },
  header: {
    display: "flex",
    justifyContent: "space-between",
    width: "100%",
    marginBottom: "10px",
    textTransform: "uppercase",
    [theme.breakpoints.down("md")]: {
      flexDirection: "column",
    },
  },
  pagination: {
    display: "flex",
    [theme.breakpoints.down("md")]: {
      justifyContent: "flex-end",
      alignItems: "flex-end",
    },
  },
  roundedButton: {
    borderRadius: "5px",
    padding: "5px",
    marginLeft: "5px",
    marginRight: "5px",
  },
  label: {
    textAlign: "left",
    fontWeight: "bold",
  },
  ellipsis: {
    margin: "0 2px",
  },
}));

const columns = [
  "id",
  "shipify",
  "date",
  "status",
  "customer",
  "email",
  "country",
  "shipping",
  "source",
  "ordertype",
];

const CustomTable = () => {
  const classes = useStyles();
  const [visibleColumns, setVisibleColumns] = useState(() => {
    const initialState = {};
    columns.forEach((column) => {
      initialState[column] = true;
    });
    return initialState;
  });
  const [selectedColumn, setSelectedColumn] = useState("all");
  const [selectedRows, setSelectedRows] = useState([]);
  const [page, setPage] = useState(1);
  const tableRef = useRef();

  const handleColumnToggle = (columnName) => {
    setSelectedColumn(columnName);
  };

  const handleRowCheckboxChange = (id) => {
    if (selectedRows.includes(id)) {
      setSelectedRows(selectedRows.filter((rowId) => rowId !== id));
    } else {
      setSelectedRows([...selectedRows, id]);
    }
  };

  const handleDispatchSelected = () => {
    const updatedVisibleColumns = {};
    columns.forEach((column) => {
      updatedVisibleColumns[column] =
        column === selectedColumn || column === "id";
    });
    setVisibleColumns(updatedVisibleColumns);
    tableRef.current.scrollIntoView({ behavior: "smooth" });
  };

  const handlePageChange = (newPage) => {
    setPage(newPage);
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const day = String(date.getDate()).padStart(2, "0");
    const month = date.toLocaleString("default", { month: "short" });
    const year = date.getFullYear();
    return `${day} ${month} ${year}`;
  };

  const totalPages = Math.ceil(data.length / 10);
  const pageNumbers = Array.from({ length: totalPages }, (_, i) => i + 1);

  return (
    <>
      <div className={classes.root}>
        <div className={classes.header}>
          <Grid
            container
            spacing={2}
            alignItems="center"
            justifyContent="space-between"
            style={{ width: "100%" }}
          >
            <Grid item xs={12} md={3}>
              <div className={classes.label}>
                <h2>Product Summary</h2>
              </div>
            </Grid>

            <Grid container item xs={12} md={6} alignItems="center">
              <Grid item xs={8} md={8} container alignItems="center">
                <Grid item style={{ margin: "10px" }}>
                  <div className={classes.label}>Show</div>
                </Grid>
                <Grid item xs={10} md={10}>
                  <Select
                    fullWidth
                    className={classes.select}
                    id="column-select"
                    value={selectedColumn}
                    onChange={(event) => handleColumnToggle(event.target.value)}
                  >
                    <MenuItem value="all">
                      <b>All columns</b>
                    </MenuItem>
                    {columns.map((column) => (
                      <MenuItem
                        key={column}
                        value={column}
                        style={{ textTransform: "capitalize" }}
                      >
                        {column}
                      </MenuItem>
                    ))}
                  </Select>
                </Grid>
              </Grid>

              <Grid item xs={4} md={4}>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleDispatchSelected}
                  disabled={!selectedColumn}
                  style={{ borderRadius: "10px", width: "100%" }}
                >
                  Dispatch Selected
                </Button>
              </Grid>
            </Grid>

            <Grid item xs={6} md={3} style={{ textAlign: "end" }}>
              <div className={classes.pagination}>
                <Button
                  className={classes.roundedButton}
                  disabled={page === 1}
                  onClick={() => handlePageChange(page - 1)}
                >
                  {"<"}
                </Button>
                {pageNumbers.slice(0, 3).map((pageNumber) => (
                  <Button
                    key={pageNumber}
                    variant={page === pageNumber ? "contained" : "outlined"}
                    color="primary"
                    style={{ borderRadius: "10px" }}
                    className={classes.roundedButton}
                    onClick={() => handlePageChange(pageNumber)}
                  >
                    {pageNumber}
                  </Button>
                ))}
                {pageNumbers.length > 3 && (
                  <>
                    <span className={classes.ellipsis}>...</span>
                    <Button
                      variant={page === totalPages ? "contained" : "outlined"}
                      color="primary"
                      className={classes.roundedButton}
                      onClick={() => handlePageChange(totalPages)}
                    >
                      {totalPages}
                    </Button>
                  </>
                )}
                <Button
                  className={classes.roundedButton}
                  disabled={page === totalPages}
                  onClick={() => handlePageChange(page + 1)}
                >
                  {">"}
                </Button>
              </div>
            </Grid>
          </Grid>
        </div>

        <TableContainer component={Paper} ref={tableRef}>
          <Table className={classes.table} aria-label="customized table">
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                {columns.map(
                  (column) =>
                    visibleColumns[column] && (
                      <TableCell
                        key={column}
                        style={{
                          textTransform: "uppercase",
                          fontWeight: "bold",
                        }}
                      >
                        {column}
                      </TableCell>
                    )
                )}
              </TableRow>
            </TableHead>
            <TableBody>
              {data.slice((page - 1) * 10, page * 10).map((row) => (
                <TableRow key={row.id}>
                  <TableCell>
                    <Checkbox
                      checked={selectedRows.includes(row.id)}
                      onChange={() => handleRowCheckboxChange(row.id)}
                      color="primary"
                    />
                  </TableCell>
                  {columns.map(
                    (column) =>
                      visibleColumns[column] && (
                        <TableCell key={column}>
                          {column === "date"
                            ? formatDate(row[column])
                            : row[column]}
                        </TableCell>
                      )
                  )}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </>
  );
};

export default CustomTable;
