import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";
import SearchIcon from "@mui/icons-material/Search";
import IconButton from "@mui/material/IconButton";
import InputAdornment from "@mui/material/InputAdornment";
import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    flexDirection: "column",
    marginBottom: theme.spacing(2),
    padding: "30px",
    borderRadius: "10px",
    width: "90%",
    backgroundColor: "#ffffff",
    margin: "20px auto",
    boxShadow: "rgba(0, 0, 0, 0.35) 0px 5px 15px",
    [theme.breakpoints.down("md")]: {
      flexDirection: "column",
      marginBottom: theme.spacing(2),
    },
  },
  textField: {
    marginBottom: theme.spacing(2),
    "& .MuiInputBase-root": {
      borderRadius: "10px",
      border: "1px solid #ccc",
    },
  },
  select: {
    marginBottom: theme.spacing(2),
    "& .MuiSelect-root": {
      borderRadius: "10px",
      border: "1px solid #ccc",
    },
  },
  searchButton: {
    textTransform: "none",
    marginBottom: theme.spacing(2),
  },
  label: {
    textAlign: "left",
    fontWeight: "bold",
    marginBottom: theme.spacing(1),
  },
}));

const CustomFilter = ({ onSearch, onCategoryChange, onStatusChange }) => {
  const classes = useStyles();
  const [searchText, setSearchText] = useState("");
  const [selectedCategory, setSelectedCategory] = useState("All");
  const [selectedStatus, setSelectedStatus] = useState("All");

  const handleSearch = () => {
    onSearch(searchText, selectedCategory, selectedStatus);
  };

  const handleCategoryChange = (event) => {
    setSelectedCategory(event.target.value);
    onCategoryChange(event.target.value);
  };

  const handleStatusChange = (event) => {
    setSelectedStatus(event.target.value);
    onStatusChange(event.target.value);
  };

  return (
    <div className={classes.root}>
      <Grid container spacing={2} alignItems="center" justifyContent="space-between">
        <Grid item xs={12} sm={12} md={3}>
          <div className={classes.label}>What are you looking for?</div>
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <IconButton>
                    <SearchIcon />
                  </IconButton>
                </InputAdornment>
              ),
            }}
            className={classes.textField}
            placeholder="Search for category, name, company, etc"
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <div className={classes.label}>Category</div>
          <Select
            fullWidth
            className={classes.select}
            label="Category"
            value={selectedCategory}
            onChange={handleCategoryChange}
          >
            <MenuItem value="All">All</MenuItem>
            <MenuItem value="Electronics">Electronics</MenuItem>
            <MenuItem value="Clothing">Clothing</MenuItem>
            <MenuItem value="Books">Books</MenuItem>
          </Select>
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <div className={classes.label}>Status</div>
          <Select
            fullWidth
            className={classes.select}
            label="Status"
            value={selectedStatus}
            onChange={handleStatusChange}
          >
            <MenuItem value="All">All</MenuItem>
            <MenuItem value="Pending">Pending</MenuItem>
            <MenuItem value="Processing">Processing</MenuItem>
            <MenuItem value="Completed">Completed</MenuItem>
          </Select>
        </Grid>
        <Grid item xs={12} sm={12} md={3}>
          <Button
            fullWidth
            variant="contained"
            color="primary"
            onClick={handleSearch}
            style={{ borderRadius: "10px" }}
          >
            SEARCH
          </Button>
        </Grid>
      </Grid>
    </div>
  );
};

export default CustomFilter;
